	* {
	    margin: 0;
	    padding: 0;
	    box-sizing: border-box;
	    font-family: "Jost", sans-serif;
	  }
	    
	  a {
	    text-decoration: none;
	  }
	  
	  li {
	    list-style: none;
	  }
	  
	 

	  
	  .main-nav {
		padding:10px;
	    background: linear-gradient(to right,  #fff, #fff);
	    width: 100%;
	    height:  5.9rem;
	    display:  grid;
	    grid-template-columns: 2rem 2fr 2fr 10rem;
	    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
	      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
	  }
	  
	  .logo {
		padding: 5px;
	    display: grid;
	    grid-column: 2/3;
	    justify-content: start;
	    align-items: center;
	  }
	  
	  .menu-link {
	    grid-column: 6/3;
	  }
	  
	  .menu-link ul {
	    height: 5.2rem;
	    display: flex;
	    justify-content: space-around;
	    align-items: center;
	  }
	  
	  .hamburger-menu{
	    grid-column: 4/5;
	  }
	  
	  .logo h2 {
	    font-size: 2.5rem;
	    font-weight: 850;
	    text-transform: uppercase;
	    background: linear-gradient(to right, #fc5c7d, #6a82fb);
	    -webkit-background-clip: text;
	    -webkit-text-fill-color: transparent;
	  }
	  
	  .logo h2 span {
	    font-size: 3.5rem;
	  }
	  
	  .menu-link ul li {
	    font-size: 1.4rem;
	  }
	  
	  .fa-cus
	  {
	  	color: #2C5364 !important;
	  }

	  .fa-home:hover
	  {
	  	color: whitesmoke;
	  }
	  
	  .menu-link ul li a {
	    text-transform: capitalize;
	    color: #2C5364;
	    padding: 6px 8px;
	    border-radius: 10px;
	    transition: 0.5s;

	    }
	  .menu-link ul li a:hover >i{
	    -webkit-transform-origin: left;
	            transform-origin: left;
	    color: white !important;
	    background:#1A2980;
	   	opacity: 1.4;
		
	    text-decoration: unset;
	    transition: 0.5s;
	  }

	  .menu-link ul li:hover > a {
	    -webkit-transform-origin: left;
	            transform-origin: left;
	    color: white !important;
	    background:#1A2980;
	   	opacity: 1.4;
	    border-radius: 10px;
	    padding: 6px 6px;
	    text-decoration: unset;
	    transition: 0.5s;
	  }

	  
	  .hamburger-menu {
	    display: none;
	  }




	  @media (max-width: 1080px) {
	    .main-nav {
	      height: 5.9rem;
	      grid-template-columns: 2rem 3fr 3fr 1fr 2rem;
	    }
	  
	    .logo,
	    .menu-link ul{
	      height: 6rem;
	    }
	  }



	  @media (max-width: 998px) {
	    .main-nav {
	      height: 5.9rem;
	      grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
	    }
	  
	    .menu-link {
	      display: none;
	    }
	  
	    .logo {
	      height: 7rem;
	    }
	  
	    .mobile-menu-link {
	      grid-column: 2/4;
	      position: relative;
	      z-index: 99;
	    }
	  
	    .mobile-menu-link {
	      background-color:whitesmoke;
	      height: 20rem;
	      display: grid;
	      grid-column: 2/5;
	      align-items: center;
	      padding-left: 3rem;
	      transition: all 1s linear;
	      -webkit-transform-origin: top;
	              transform-origin: top;
	      box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
	        rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
	    }
	  
	    .mobile-menu-link ul {
	      height: 20rem;
	      display: flex;
	      justify-content: space-around;
	      flex-direction: column;
	      align-items: start;
	    }
	  
	    .mobile-menu-link ul li:first-child {
	      transition-delay: 0.2s;
	    }
	    .hamburger-menu {
	      grid-row: 1/2;
	      grid-column: 3/5;
	      justify-items: end;
	      transition: all 2s linear;
	      height: 7rem;
	      display: flex;
	      justify-self: end;
	      align-items: center;
	    
	      display: block;
	      font-size: 2.2rem;
	    }
	  }
	  
	  @media (max-width: 798px) {
	    .main-nav {
	      height: 5.9rem;
	      grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
	    }
	  
	    .logo {
	      height: 6rem;
	    }
	  
	    .hamburger-menu {
	      height: 6rem;
	      justify-self: end;
	      align-items: center;
	      display: block;
	      font-size: 2.5rem;
	    }
	  }
	  
	  @media (max-width: 520px) {
	    .main-nav {
	      height: 5.9rem;
	      grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
	    }
	  
	    .logo {
	      height: 6rem;
	    }
	  
	    .logo h2 {
	      font-size: 2rem;
	    }
	    .hamburger-menu {
	      height: 6rem;
	      justify-self: end;
	      align-items: center;
	      display: block;
	      font-size: 2.5rem;
	    }
	  
	  }

